export default {
  layout: "Dum-content",
  mounted() {
    this.getslider();
  },
  data() {
    return {
      sliders: [],
      slider: {
        title: "",
        description: "",
        image: "",
        slug: "",
      },
      errors: {},
      base_url: process.env.url
    };
  },
  methods: {
    getslider() {
      this.$axios
        .$get("/slider/show")
        .then((res) => {
          this.sliders = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    sliderEdit(id) {
      $("#sliderUpdate").modal("show");
      this.$axios
        .$get("/slider/edit/" + id)
        .then((res) => {
          this.slider = res;

        })
        .catch((err) => {
          console.log(err);
        });

    },
    sliderUpdate() {
      let formData = new FormData();
      formData.append('title', this.slider.title)
      formData.append('description', this.slider.description)
      formData.append('image', this.slider.image)
      this.$axios
        .$post("/slider/update/" + this.slider.id, formData)
        .then((res) => {
          this.getslider();
          $("#sliderUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";
        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    deleteslider(id) {
      if (confirm("Are you sure to delete this slider?")) {
        this.$axios
          .$get("/slider/delete/" + id)
          .then((res) => {
            this.getslider();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }

    },
    sliderStatus(id) {
      this.$axios
        .$get("/slider/status/" + id)
        .then((res) => {
          this.getslider();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },









  }
};
